<template>
  <div class="content">
    <!-- 问题列表 -->
    <div class="_bg"></div>
    <div class="plcaholder"></div>
    <div
      class="mask"
      v-if="maskShow"
      v-loading="true"
      element-loading-text="提交上传中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
    ></div>

    <div v-if="question_choice.length || question_essay.length">
      <!-- 选择题 -->
      <el-card v-for="(question, i) in question_choice" :key="question.id">
        <strong>{{ i + 1 }}. {{ question.title }}</strong
        ><br />
        <el-row>
          <span v-for="(option, index) in question.options" :key="index">
            <el-checkbox v-model="option.value"
              >{{ String.fromCharCode(97 + index) }}.
              {{ option.dec }}</el-checkbox
            >
          </span>
        </el-row>
        <el-row v-if="showAnswer" style="font-size: 16px"
          >答案：<span
            v-for="(item, a) in cq_data[i].answer"
            :key="a"
            style="color: red"
            >{{ item }}</span
          >
        </el-row>
      </el-card>
      <!-- 问答题 -->
      <!--
      <el-card
        v-for="(question, i) in question_essay"
        :key="question.id + 'eq'"
      >
        <strong
          >{{ question_choice.length + i + 1 }}. {{ question.title }}</strong
        ><br /><br />
        <el-input
          type="textarea"
          :rows="6"
          placeholder="请在此填写答案..."
          v-model="question.answer"
        ></el-input>
        <el-row v-if="showAnswer" style="font-size: 16px"
          >答案： {{ eq_data[i].answer }}</el-row
        >
      </el-card>
      -->
      <el-row class="submit-btn">
        <el-button type="primary" @click="submit">提交</el-button></el-row
      >
    </div>
    <el-empty description="暂无测试题" v-else> </el-empty>
  </div>
</template>

<script>
import { getRand } from '@/api/question'
export default {
  data () {
    return {
      maskShow: false,
      showAnswer: false,
      // 选择题
      question_choice: [],
      // 问答题
      question_essay: [],
      // 真实数据
      cq_data: [],
      eq_data: []
    }
  },
  methods: {
    // 获取随机问题
    async getQuestions () {
      const data = await getRand()
      this.cq_data = JSON.parse(JSON.stringify(data.question_choice))
      this.eq_data = JSON.parse(JSON.stringify(data.question_essay))
      this.cq_data.forEach((item) => {
        item.answer = []
        item.options.forEach((val, i) => {
          if (val.value) item.answer.push(String.fromCharCode(97 + i))
        })
      })
      this.question_choice = data.question_choice
      this.question_choice.forEach((question) => {
        question.options.map((val) => (val.value = false))
      })
      this.question_essay = data.question_essay
      this.question_essay.map((val) => (val.answer = ''))
    },
    submit () {
      this.maskShow = true
      const timer = setTimeout(() => {
        this.maskShow = false
        this.showAnswer = true
        clearTimeout(timer)
      }, 3000)
    }
  },
  created () {
    this.getQuestions()
  }
}
</script>

